.sidenav-container {
  height: calc(100vh - 64px);
  position: sticky;

  @include respond(compact) {
    height: calc(100vh - 56px); // If mat-toolbar is there
    // height: 100vh; // Without mat-toolbar
  }
}

.bottom-sidenav-container {
  // height: calc(100vh - 70px);
}

.sidenav {
  width: auto;
  border: none;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;

  @include respond(compact) {
    top: auto;
    left: 0;
    right: 0;
  }
}

.custom-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;

  .nav-drawer {
    //----------- DEMO TEMP ---------//
    margin-top: -24px;
    //-----------  ---------//

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 6px;

    .custom-fab {
      box-shadow: none;
    }

    .nav-list-wrapper {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 80px;

      &:first-of-type {
        margin-top: 30px;
      }

      .active-link {
        text-align: center;
        width: 48px;
        height: 30px;
        border-radius: 100px;
      }

      .nav-list {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-decoration: none;
        // When enabled, it makes flickers!! This should be improved!
        transition: width 0.3s ease-in-out;

        &:focus,
        &:active {
          outline: none;
          border-radius: 100px;
          -webkit-tap-highlight-color: transparent;
          touch-action: none;
        }

        mat-icon {
          height: 30px;
          line-height: 1.2;
        }

        &:hover {
          width: 3rem;
          text-align: center;
          width: 48px;
          height: 30px;
          border-radius: 100px;
        }
      }

      span {
        font-size: 12px;
        font-weight: 600;
      }
    }
  }

  &.expanded-custom-nav {
    align-items: flex-start;
    padding-left: 12px;

    .nav-drawer {
      gap: 0;
      width: 360px;

      .custom-fab {
        align-self: flex-start;
        margin-bottom: 16px;
      }

      .nav-list-wrapper {
        align-items: start;
        width: 360px;
        padding-right: 12px;

        &:first-of-type {
          margin-top: 0;
        }

        .nav-list {
          flex-direction: row;
          gap: 16px;
          width: 100%;
          padding: 0 16px;
          height: 56px;

          &:hover {
            width: 100%;
            height: 56px;
            border-radius: 100px;
          }
        }
      }
    }
  }

  @include respond(compact) {
    .nav-drawer {
      flex-direction: row;
      justify-content: space-around;
      height: 64px;
      margin-top: 0;
      width: 100%;
    }
  }
}

.navigation-sidenav {
  height: inherit;
  overflow: inherit;

  .main {
    height: calc(100vh - 78px);
    overflow-y: hidden !important;
    border-radius: 16px;
    padding: 16px;
    margin-right: 16px;
    margin-bottom: 16px;
    // height: calc(100vh - 200px);
  }

  @include respond(compact) {
    margin-left: 0 !important;
    height: calc(100vh - 56px); // If mat-toolbar is there

    .main {
      padding: 0;
      // margin: 0;
      height: calc(100vh - 136px);

      overflow-y: scroll !important;
      border-radius: 0;
      margin-right: 0;
      margin-bottom: 0;
    }
  }
}

footer {
  position: absolute;
  bottom: -11px;
  right: 18px;
  font-size: 10px;
}
